 :style=\"'height:' + tableBodyHeight + 'px;'\">\n            <div class=\"table-row\" v-for=\"(variable, index) in props.model.processVariables\">\n                <div class=\"row-parameter\" :title=\"variable.key\">{{ variable.key }}</div>\n\n                <div v-if=\"!props.editable\" class=\"row-data-type data-type\">{{ variable.type }}</div>\n                <div v-else class=\"row-data-type data-type-editable\">\n                    <select\n                        v-if=\"variable.addedByUser\"\n                        class=\"data-type-combo\"\n                        style=\"color: #212121\"\n                        v-model=\"variable.type\"\n                        @change=\"handleDataTypeChange(variable)\"\n                    >\n                        <option v-for=\"dataType in dataTypes\" :selected=\"dataType == variable.type\">{{ dataType }}</option>\n                    </select>\n                    <input\n                        v-else\n                        type=\"text\"\n                        class=\"data-type-combo\"\n                        style=\"background-color: #e3e3e3\"\n                        :id=\"variable.key\"\n                        :name=\"variable.key\"\n                        :value=\"variable.type\"\n                        :readonly=\"!variable.addedByUser\"\n                        :disabled=\"!variable.addedByUser\"\n                    />\n                </div>\n\n                <div v-if=\"!props.editable\" class=\"row-current-value current-value\">\n                    {{ screens.find((screen) => screen.ID == variable.value)?.name || variable.value || \"-\" }}\n                </div>\n                <div v-else class=\"row-current-value current-value-editable\">\n                    <input\n                        v-if=\"(variable.type == 'String' && variable.key != 'instUnitHierarchy') || variable.type === 'Number'\"\n                        type=\"text\"\n                        class=\"current-value-input\"\n                        @input=\"modifyModel\"\n                        :id=\"variable.key\"\n                        :name=\"variable.key\"\n                        v-model=\"variable.value\"\n                    />\n                    <select\n                        v-if=\"variable.type == 'String' && variable.key == 'instUnitHierarchy'\"\n                        class=\"current-value-input\"\n                        v-model=\"variable.value\"\n                        :style=\"'color:' + (String(variable.value).length == 0 ? '#A1A1A1' : 'black')\"\n                    >\n                        <option style=\"color: black !important\">SAME</option>\n                        <option style=\"color: black !important\">PARENT</option>\n                    </select>\n                    <select\n                        v-if=\"variable.type == 'Boolean'\"\n                        class=\"current-value-input\"\n                        v-model=\"variable.value\"\n                        @change=\"handleBoolean(variable)\"\n                        :style=\"'color:' + (String(variable.value).length == 0 ? '#A1A1A1' : 'black')\"\n                    >\n                        <option value=\"\" disabled :selected=\"variable.value == ''\">Select</option>\n                        <option style=\"color: black !important\">false</option>\n                        <option style=\"color: black !important\">true</option>\n                    </select>\n                    <select\n                        v-if=\"variable.type == 'Screen'\"\n                        class=\"current-value-input\"\n                        @change=\"handleScreen($event, variable)\"\n                        :style=\"'color:' + (screens.some((screen) => screen.ID == variable.value) ? 'black' : '#A1A1A1')\"\n                    >\n                        <option value=\"\" disabled :selected=\"!screens.some((screen) => screen.ID == variable.value)\">Select</option>\n                        <option\n                            v-for=\"screen in props.screens\"\n                            :id=\"screen.ID\"\n                            :selected=\"variable.value == screen.ID\"\n                            style=\"color: black !important\"\n                        >\n                            {{ screen.name }}\n                        </option>\n                    </select>\n                    <select\n                        v-if=\"variable.type == 'Http Method'\"\n                        class=\"current-value-input\"\n                        v-model=\"variable.value\"\n                        @change=\"modifyModel\"\n                        :style=\"'color:' + (restMethods.some((method) => method == variable.value) ? ' black' : '#A1A1A1')\"\n                    >\n                        <option value=\"\" disabled :selected=\"!restMethods.some((method) => method == variable.value)\">Select</option>\n                        <option v-for=\"restMethod in restMethods\" style=\"color: black !important\">{{ restMethod }}</option>\n                    </select>\n                </div>\n\n                <div class=\"row-default-value\" :title=\"String(getDefaultValue(variable.key, props.state.processName, props.state.moduleName))\">\n                    {{ String(getDefaultValue(variable.key, props.state.processName, props.state.moduleName)) || \"-\" }}\n                </div>\n                <div v-if=\"props.editable\">\n                    <i v-if=\"variable.addedByUser\" class=\"mdi mdi-delete row-operation\" @click=\"deleteVariable(index)\"></i>\n                    <i v-else class=\"mdi mdi-arrow-u-left-top row-operation\" @click=\"returnToDefaultValue(variable)\"></i>\n                </div>\n            </div>\n        </div>\n        <div v-else class=\"empty-table-information\">\n            <img src=\"../../../../../images/no_results_found.svg\" class=\"no-results-found-img\" alt=\"No results found.\">\n            <div class=\"no-results-found-text\">No results found to fill this table.</div>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref, computed, watch } from \"vue\";\nimport { ILocalization } from \"../../../../../domain/infrastructure/ILocalization\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IModel } from \"../../../../../domain/model/models\";\nimport { ModifiedCallbackType } from \"../../../../../domain/model/editors/modifiedCallbackType\";\nimport { IProcessEditorState } from \"../../../../../domain/presentation/editors/IProcessEditorModel\";\nimport { IProcessModel, IProcessVariable } from \"../../../../../../../common/qCloudTemp/process\";\nimport { IProcessEditorUtil } from \"../../../../../domain/useCase/IProcessEditorUtil\";\n\ninterface IProcessVariableDataTableProps {\n    editable?: boolean;\n    top?: string;\n    screens: Array<IModel>;\n    modified: ModifiedCallbackType;\n    resizeWizard: (height?: number) => void;\n    state: IProcessEditorState;\n    model: IProcessModel;\n}\n\nconst props = defineProps<IProcessVariableDataTableProps>();\nconst processEditorUtil = container.resolve<IProcessEditorUtil>(IProcessEditorUtil);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst tableHeaders = ref([\"parameter\", \"dataType\", \"currentValue\", \"defaultValue\", \"operations\"]);\nconst dataTypes = [\"String\", \"Boolean\", \"Screen\", \"Number\", \"Http Method\"];\nconst restMethods = [\"GET\", \"POST\", \"PUT\", \"PATCH\", \"DELETE\", \"HEAD\", \"CONNECT\", \"OPTIONS\", \"TRACE\"];\n\nconst tableBodyHeight = computed(() =>\n    props.model.processVariables.length < 7 ? 264 : props.model.processVariables.length < 9 ? props.model.processVariables.length * 44 : 396\n);\n\nconst wizardHeight = computed(() =>\n    props.model.processVariables.length < 7\n        ? 624\n        : props.model.processVariables.length < 9\n        ? 624 + (props.model.processVariables.length - 6) * 44\n        : 754\n);\n\nprops.resizeWizard(wizardHeight.value);\nwatch(wizardHeight, () => {\n    props.resizeWizard(wizardHeight.value);\n});\n\nfunction deleteVariable(index: number) {\n    props.model.processVariables.splice(index, 1);\n    modifyModel();\n}\n\nfunction returnToDefaultValue(variable: IProcessVariable) {\n    variable.value = getDefaultValue(variable.key, props.state.processName, props.state.moduleName);\n    modifyModel();\n}\n\nfunction getDefaultValue(key: string, processName: string, moduleName: string) {\n    const defaultValue = processEditorUtil.getDefaultVariable(key, processName, moduleName);\n    return defaultValue != undefined ? defaultValue : \"\";\n}\n\nfunction handleDataTypeChange(variable: IProcessVariable) {\n    variable.value = \"\";\n    modifyModel();\n}\n\nfunction handleScreen($event: any, variable: IProcessVariable) {\n    const selectedScreen = $event.target[$event.target.selectedIndex];\n    variable.value = selectedScreen.id;\n    modifyModel();\n}\n\nfunction handleBoolean(row: IProcessVariable) {\n    row.value = row.value == \"true\";\n    modifyModel();\n}\n\nfunction modifyModel() {\n    props.modified({ model: true });\n    props.state.modified = true;\n}\n</script>\n\n<style scoped>\n.data-table {\n    position: absolute;\n    top: 182px;\n    left: 32px;\n    width: 936px;\n    height: 436px;\n}\n\n.table-header-text {\n    top: 11px;\n    color: #373737;\n    font-size: 13px;\n    font-family: \"Roboto\";\n    font-weight: 500;\n}\n\n.table-header {\n    position: absolute;\n    top: 0px;\n    left: 0px;\n    width: 100%;\n    height: 40px;\n    background: #f5f5fa 0% 0% no-repeat padding-box;\n}\n\n.header-parameter {\n    position: absolute;\n    left: 12px;\n}\n\n.header-dataType {\n    position: absolute;\n    left: 193px;\n}\n\n.header-currentValue {\n    position: absolute;\n    left: 395px;\n}\n\n.header-defaultValue {\n    position: absolute;\n    left: 663px;\n}\n\n.table-body {\n    position: absolute;\n    top: 40px;\n    width: 936px;\n    border: none;\n    overflow-x: hidden;\n    overflow-y: auto;\n}\n\n.table-body::-webkit-scrollbar {\n    width: 4px;\n}\n\n.table-body::-webkit-scrollbar-thumb {\n    background: #bcbcbc 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n}\n\n.table-body::-webkit-scrollbar-thumb:hover {\n    background: #555;\n}\n\n.table-row {\n    width: 100%;\n    height: 44px;\n    display: flex;\n    border-bottom: 1px solid #e0e0e0;\n}\n\n.row-parameter {\n    margin-top: 14px;\n    margin-left: 12px;\n    font-size: 12px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 140px;\n    display: inline-block;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.row-data-type {\n    margin-left: 41px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 154px;\n    display: inline-block;\n}\n\n.data-type {\n    margin-top: 12px;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.data-type-editable {\n    margin-top: 7px;\n}\n\n.data-type-combo {\n    width: 154px;\n    height: 30px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    padding-left: 6px;\n    font: normal normal normal 14px/19px Roboto;\n}\n\n.current-value-input {\n    width: 220px;\n    height: 30px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    padding-left: 10px;\n    font: normal normal normal 14px/19px Roboto;\n}\n\n.row-current-value {\n    margin-left: 48px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 220px;\n    display: inline-block;\n}\n\n.current-value {\n    margin-top: 12px;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.current-value-editable {\n    margin-top: 7px;\n}\n\n.row-default-value {\n    margin-left: 48px;\n    margin-top: 12px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 209px;\n    display: inline-block;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.row-operation {\n    margin-top: 11px;\n    margin-left: 12px;\n    font: normal normal normal 20px/25px Material Design Icons Desktop;\n    cursor: pointer;\n    display: inline-block;\n}\n\n.empty-table-information {\n    position: absolute;\n    top: 65px;\n    left: 352px;\n    border: none;\n}\n\n.no-results-found-img {\n    margin-left: 53px;\n}\n\n.no-results-found-text {\n    font: normal normal normal 16px/21px Roboto;\n    color: #A1A1A1;\n    margin-top: 12px;\n}\n</style>\n"],"sourceRoot":""}]);
